const duration = 2000;
import md5 from '@/js_sdk/ccdzhang-dokey/md5.js';
import http from '@/components/utils/http.js';
import { mapState, mapMutations } from 'vuex';

export default {
	components: {},
	data() {
		return {
			backpage: '../index/home',
			loginMode: 1,
			usernameType: 'text',
			codeBut: '获取验证码',
			codeClick: true,
			mobile: '',
			password: '',
			code: '',

			SessionKey: '',
			OpenId: '',
			nickName: null,
			avatarUrl: null,
			bindmobile: false,
			Code:"",
			userinfo:[],
			vcode:""
		};
	},
	computed: mapState(['uerInfo', 'hasLogin']),
	methods: {
		...mapMutations(['login']),
		showModal() {
			this.bindmobile = true;
		},
		hideModal(e) {
			this.bindmobile = false;
		},
		loginCode() {
			this.loginMode = 2;
		},
		loginmobile() {
			this.loginMode = 1;
		},
		// 手机号输入
		mobileInput(e) {
			this.mobile = e.detail.value;
		},
		// 密码输入
		passwordInpur(e) {
			this.password = e.detail.value;
		},
		// 验证码输入
		codeInput(e) {
			this.code = e.detail.value;
		},
		// 获取验证码
		getCode() {
			var _that = this;

			if (_that.codeClick) {
				if (_that.mobile[0] != 1 || _that.mobile.length != 11) {
					this.$api.msg('请输入正确的手机号');
					return;
				} else {
					_that.codeClick = false;
					// 发送短信
					_that._sendmsm(_that.mobile);
					// 倒计时开始
				}
			}
		},
		// 点击登录
		loginUp() {
			var _that = this;
			if (_that.mobile == '') {
				this.$api.msg('请输入手机号码');
				return;
			} else if (!/(^1[3|4|5|7|8][0-9]{9}$)/.test(_that.mobile)) {
				this.$api.msg('手机号码格式不正确');
				return;
			} else if (_that.password == '' && _that.loginMode == 1) {
				this.$api.msg('请输入密码');
				return;
			} else if (_that.code == '' && _that.loginMode == 2) {
				this.$api.msg('请输入验证码');
				return;
			} else {
				const data = {
					loginmode: _that.loginMode,
					mobile: _that.mobile,
					password: md5(md5(_that.password)),
					code: _that.code,
					vcode:_that.vcode,
				};
				this._logonrequest(data);
			}
		},

		_logonrequest(userdata) {
			let opts = {
				url: '/userApi/logon/',
				method: 'post'
			};
			let param = userdata;
			http.httpRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						this.login(res.data['token']);
						uni.navigateTo({
							url: this.backpage
						});
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		_sendmsm(mobile) {
			var _that = this;

			let opts = {
				url: '/CommonApi/sendmsm/',
				method: 'post'
			};
			let param = { mobile: mobile, sendtype: 'logon' };
			http.httpRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						var daojishi = 60;
						var xianshi = setInterval(function() {
							_that.codeBut = '重新发送' + daojishi;
							daojishi--;
							if (daojishi <= 0) {
								_that.codeBut = '重新发送';
								_that.codeClick = true;
								clearInterval(xianshi);
							}
						}, 1000);
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete => {
					this.loading = false;
				}
			);
		},

		getPhoneNumberHander: function(e) {
			
			console.log("e",e);
			
			var that = this; // 拒绝授权

			if (e.detail.errMsg == 'getPhoneNumber:fail user deny') {
				wx.showModal({
					title: '提示',
					showCancel: false,
					content: '未授权您将无法登陆',
					success: function(res) {}
				});
			} else {
				// 接受授权
				let opts = {
					url: '/CommonApi/getphone/',
					method: 'post'
				};
				let param = {
					encryptedData: e.detail.encryptedData,
					iv: e.detail.iv,
					//openid: this.OpenId,
					session_key: this.SessionKey,
				};
				http.httpRequest(opts, param).then(
					res => {
						//打印请求返回的数据
						if (res.data['code'] == 0) {
							//获得手机号码注册
							
							that.weixinreg(res.data['phone']);
							console.log(res.data);
						} else {
							this.$api.msg(res.data.msg);
						}
					},
					error => {
						console.log(error);
					}
				);
				
			}
		},
		weixinreg(mobile){
			var _that = this;
			let opts = {
				url: '/UserApi/wxreg/',
				method: 'post'
			};
			let param = {
				mobile:mobile ,
				wxopenid:this.OpenId,
				nickname: this.userinfo["nickName"],
				province: this.userinfo["province"],
				// headimg: this.userinfo["avatarUrl"],
				city: this.userinfo["city"],
				country: this.userinfo["country"],
				gender: this.userinfo["gender"],
				vcode:_that.vcode,
			};
			http.httpRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						//获得手机号码注册
						if(res.data['token']!=""){
							//已经存在用户 ，登录
							_that.login(res.data['token']);
							uni.navigateTo({
								url: _that.backpage
							});
						}
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
				}
			);
			
			
		},
		weixinlogin() {
			let _this = this;
			uni.showLoading({ title: '登录中...' });
			uni.getProvider({
				service: 'oauth',
				success: function(res) {
					if (~res.provider.indexOf('weixin')) {
						uni.login({
							provider: 'weixin',
							success: function(loginRes) {
								let code = loginRes.code;
								//this.code=code;
								 console.log(loginRes);
								// 获取用户信息
								uni.getUserInfo({
									provider: 'weixin',
									success: function(infoRes) {
										//获取用户信息后向调用信息更新方法
										console.log(infoRes.userInfo);
										let userinfo = infoRes.userInfo;
										_this.userinfo=userinfo;
									}
								});
								//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
								let opts = {
									url: '/CommonApi/getwxopenid/',
									method: 'post'
								};
								let param = { code: code };
								_this.Code=code;
								http.httpRequest(opts, param).then(
									res => {
										//打印请求返回的数据
										if (res.data['code'] == 0) {
											if(res.data['token']!=""){
												//已经存在用户 ，登录
												_this.login(res.data['token']);
												uni.navigateTo({
													url: _this.backpage
												});
											}else{
												let sessionkey = res.data['data'].session_key;
												let openid = res.data['data'].openid;
												_this.OpenId=openid;
												_this.SessionKey=sessionkey;
												_this.showModal();
											}
											
											uni.hideLoading();
										} else {
											this.$api.msg(res.data.msg);
										}
									},
									error => {
										console.log(error);
									}
								);
							}
						});
					}
				}
			});
		}
	},

	onLoad(options) {
		if (global.islogon()) {
			uni.redirectTo({
				url: '../person/home'
			});
		}
		if (options.backpage!=undefined) {
			this.backpage = options.backpage;
		}
		
		try {
		    const value = uni.getStorageSync('vcode');
		    if (value) {
		       this.vcode=value;
		    }
		} catch (e) {
		    // error
		}
	
	}
};
